'use strict'
import React, {PropTypes} from 'react';
// import videoPlayer from '../../services/videoPlayer';
import YouTubePlayer from 'youtube-player';
import SVG from './SVG.js';
import SvgVideoController from './SvgVideoController.js';
require('../../images/icons.svg');

const playerStates = {
  "UNSTARTED": -1,
  "ENDED": 0,
  "PLAYING": 1,
  "PAUSED": 2,
  "BUFFERING": 3,
  "CUED": 5
};
// import 'https://www.youtube.com/iframe_api?noext'

// Trying a 100% pure React Solution for the Youtube Player

class VideoEmbed extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      progress: 0,
      showVolume: false,
      captions: false
    };
    this.firstLoad = true;
    this.seekToSec = null;
    this.hover = this.hover.bind(this);
    this.mouseOut = this.mouseOut.bind(this);
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.playAt = this.playAt.bind(this);
    this.startTrackingTime = this.startTrackingTime.bind(this);
    this.clickProgress = this.clickProgress.bind(this);
    this.getShareLink = this.getShareLink.bind(this);
    this.hideShareLink = this.hideShareLink.bind(this);
    this.handleVolumeChange = this.handleVolumeChange.bind(this);
    this.showVolume = this.showVolume.bind(this);
    this.hideVolume = this.hideVolume.bind(this);
    this.showCaptions = this.showCaptions.bind(this);
    this.toggleCaptions = this.toggleCaptions.bind(this);
    this.hideCaptions = this.hideCaptions.bind(this);
    this.getFullScreen = this.getFullScreen.bind(this);
    this.showMedia = this.showMedia.bind(this);
    this.clickMedia = this.clickMedia.bind(this);
    this.mouseOverMedia = this.mouseOverMedia.bind(this);
    this.mouseOutMedia = this.mouseOutMedia.bind(this);
    this.player;
  }
  onPlayerStateChange(evt) {
    if(evt.data === 1) {
      this.setState({playing: true});
      this.interval = setInterval(this.startTrackingTime, 500);
    } else {
      this.setState({playing: false});
      clearInterval(this.interval);

    }

    if(this.state.playerState == playerStates.PLAYING && evt.data == playerStates.PAUSED) {
      this.player.getCurrentTime().then((time) => {
        let progress = (time/this.state.duration) * 100;
        this.setState({progress})
        // console.log(`PROGRESS ${percentage}%`);
        // this.progress.style.cssText = `width:${percentage}%`;
        this.seekToSec = time
      });
    }

    if (this.firstLoad && evt.data == playerStates.PLAYING) {
      this.firstLoad = false;
      // this.player.getDuration().then((time) => {
      //   this.duration = time;
      // });
      this.hideCaptions()
      this.pause()
    }


    this.setState({playerState: evt.data})
  }
  onPlayerReady(){
    this.hideCaptions()
    const player = this.player
  }
  setVolume(number){
    this.player.setVolume(number)
    this.setState({volume: number});
  }
  showMedia() {
    this.pause()
    this.props.showMedia(this.state.media)
  }
  clickMedia(media) {
    this.props.showMedia(media)
  }
  mouseOverMedia(media) {
    this.setState({showMediaTitle: media})
  }
  mouseOutMedia(media) {
    this.setState({showMediaTitle: null})
  }
  play(time){
    if (this.player) {
      if (this.seekToSec) {
        this.player.seekTo(parseInt(this.seekToSec), true).then(()=>{
          this.seekToSec = null
        })
      }
      this.player.playVideo();
    }
  }
  pause(){
    this.player.pauseVideo();
  }
  playAt(time){
    this.seekToSec = time
    if (this.player) {
      this.play(time)
    } 
  }
  startTrackingTime(){
    const {data} = this.props;
    this.player.getCurrentTime().then((time) => {
      for (let media of data.media) {
        if(time > media.start && time < media.end) {
          if(!this.state.media) {
            this.setState({media: media})
          }
        } else if(this.state.media){
          if (this.state.media.start == media.start) {
            this.setState({media: null})
          }
        }
      }
    });
  }
  handleVolumeChange(event) {
    this.player.getOptions('captions').then((cap) => {
    });
    this.setVolume(event.target.value)
  }
  toggleCaptions() {
    if (this.state.captions) {
      this.hideCaptions()
    } else {
      this.showCaptions()
    }
  }
  showCaptions() {
    this.player.loadModule("captions");  //Works for html5 ignored by AS3
    this.player.loadModule("cc");
    this.setState({captions: true})
  }
  hideCaptions(){
    this.player.unloadModule("captions");  //Works for html5 ignored by AS3
    this.player.unloadModule("cc");
    this.setState({captions: false})
  }
  showVolume(evt){
    this.setState({showVolume: true});
  }  
  hideVolume(evt){
    this.setState({showVolume: false});
  }
  getShareLink(evt){
    if (!this.url) {
      this.player.getVideoEmbedCode().then(url => {
        this.url = url;
        this.setState({shareLink: url})
      })
    } else {
      this.setState({shareLink: this.url})
    }
  }
  hideShareLink(){
    this.setState({shareLink: null})
  }
  clickProgress(evt){
    const leftOffset = this._progressBar.getBoundingClientRect().left; 
    const fullWidth = this._progressBar.offsetWidth;
    const percentage = ((evt.pageX - leftOffset)/fullWidth) * 100;
    const vidSeconds = (this.state.duration/100)*percentage;
    this.seekToSec = vidSeconds
    this.seekTo(vidSeconds)
    
    let progress = (vidSeconds/this.state.duration) * 100;
    this.setState({progress})

    // this.playAt(vidSeconds)
    // console.log("click progress", vidSeconds);
  }
  componentDidMount(){
    const {data, height, width} = this.props;

    this.player = YouTubePlayer(this._vidDiv, {
          height: height,
          width: width,
          videoId: data.id,
          playerVars: {
            controls: 0,
            showinfo: 0
          },
        });

    this.play()
    // .then(x => {
    //   // console.log('x', x);
    //   // this.hideCaptions()
    //   setTimeout(() => {this.hideCaptions();this.pause();}, 3000)
    // });

    this.player.getDuration().then((time) => {
      // this.duration = time;
      this.setState({duration: time})
    });
    this.player.getVolume().then((volume) => {
      this.setState({volume: volume})
    });
    this.player.getIframe().then(iframe => {
      this._iframe = iframe
    });
    // this.hideCaptions()

    // this.pause()


    // this.player
    //   .stopVideo()
    //   .then(() => {
    //       // Every function returns a promise that is resolved after the target function has been executed.
    //   });

    this.player.on('ready', (evt) => {
      this.onPlayerReady()
    });   

    this.player.on('stateChange', (evt) => {
      this.onPlayerStateChange(evt)
    });


  }

  prettifyMinutes(time) {
    time = parseInt(time)
    let minutes = Math.floor(time / 60);
    let seconds = time - minutes * 60;

    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (seconds.toString().length === 1) {
      seconds = "0" + seconds;
    }
    return `${minutes}:${seconds}`;
  }

  seekTo(time){
    this.player.seekTo(parseInt(time), true);
    this.pause()
  }

  hover(part) {
    this.seekTo(part.start)
    this.setState({showDetails: part})
  }

  mouseOut(){
    this.setState({showDetails: null})
    if (this.seekToSec) {
      this.player.seekTo(parseInt(this.seekToSec), true)
    }
  }
  
  getFullScreen(){
    const requestFullScreen = this._iframe.requestFullScreen || this._iframe.mozRequestFullScreen || this._iframe.webkitRequestFullScreen;
    if (requestFullScreen) {
      requestFullScreen.bind(this._iframe)();
    }
  }

  render() {
    const {data, height, width} = this.props;
    let media = data.media ? data.media : [];
    const details = this.state.showDetails
    const style = {
      width,
      height
    }

    let chapterDetails;
    let volume;

    if (this.state.showVolume){
      volume = (
        <div style={{border: "1px solid white", alignSelf: "flex-end"}}>
          <p onClick={this.hideVolume}>X</p>
          <input type="number" min="0" max="100" value={this.state.volume} onChange={this.handleVolumeChange}/>
        </div>
      )
    } else {
      volume = <span className="flex v-center"><IconVolume onClick={this.showVolume} stroke="white" fill="white" strokeWidth="1"/></span>
    }

    let share;
    if (this.state.shareLink) {
      share = <p onClick={this.hideShareLink} style={{position: "absolute", backgroundColor: "rgba(0,0,0,0.9)", zIndex: 1, borderRadius: "10px"}}>{this.state.shareLink}</p>
    } else {
      share = <span onClick={this.getShareLink} className="flex v-center"><IconShare stroke="white" strokeWidth="1"/><p style={{alignSelf: "flex-end"}}>Share</p></span>
    }

    if(details) {
      chapterDetails = (
        <div className="width60 flex">
          <div className="container">{data.title}</div>
          <div className="chapter-details width100 container">
            <div>
              <h6>Chapter:</h6>
              <div className="flex">
                <span className="block-num">{data.parts.indexOf(details)+1}</span>
                <div>
                  <p style={{borderBottom: "1px solid white", padding: 0, margin: 0}}>{details.title}</p>
                  <p>{this.prettifyMinutes(details.start)} / {this.prettifyMinutes(this.state.duration)}</p>
                </div>
              </div>
            </div>
            <div>              
              <h6>Summary:</h6>
              <p>{details.description}</p>
            </div>
          </div>
        </div>
      )
    } else {
      chapterDetails = <div className="width60 flex"></div>
    }

    let controls;
    if (this.state.playing) {
      controls = null;
    } else {
    controls = (
      <div className="gradient">
        <div className="flex" style={{position: "absolute", height: "100%", width: "100%", alignItems: "center", justifyContent: "center"}}>
          <SVG style={{height: "150px", width: "150px"}} >
            <SvgVideoController 
              height={150} 
              width={150} 
              data={data}
              hover={this.hover}
              mouseOut={this.mouseOut}
              play={this.play}
              playAt={this.playAt} />
          </SVG>
        </div>
        <div style={{pointerEvents: "none", position: "absolute", height: "100%", width: "100%", display: "flex"}}>
          {chapterDetails}
          <div className="player-btn width40 flex container" style={{pointerEvents: "initial"}}>
            <div className="width100 flex" style={{flexDirection: "row-reverse"}}>
              <IconFullScreen onClick={this.getFullScreen} stroke="white" strokeWidth="3"/>
            </div>
            <div className="flex width100" style={{alignSelf: "flex-end", justifyContent: "space-around"}}>
              {share}
              <p onClick={this.toggleCaptions} className={this.state.captions ? "captions focused": "captions"} style={{alignSelf: "flex-end"}}>CC</p>
              {volume}
            </div>
          </div>
          <div 
            onClick={this.clickProgress} 
            className="" 
            style={{ cursor: "pointer", alignSelf: "flex-end", pointerEvents: "initial", position: "absolute", borderTop: "1px solid rgba(255,255,255,0.5)", width: "100%", height: "9px"}} 
            ref={(c) => this._progressBar = c} >
            <div className="width100">
              <div style={{backgroundColor: "#AB0015", width: this.state.progress + "%", height: "5px", marginTop: "3px"}}>
              </div>
            </div>
          </div>
          <div 
            className="flex" 
            style={{ cursor: "pointer", alignSelf: "flex-end", pointerEvents: "none", position: "absolute", borderTop: "1px solid rgba(255,255,255,0.5)", width: "100%", height: "9px"}}>
            <div className="width100" style={{position: "relative"}}>
            {
                media.map((x) => {
                  if(this.state.duration) {
                    let margin = (x.start/this.state.duration) * 100;
                    return <div onMouseOver={() => {this.mouseOverMedia(x)}}  onMouseOut={this.mouseOutMedia} onClick={() => {this.clickMedia(x)}} style={{marginLeft: `${margin}%`, position: "absolute", backgroundColor: "#959D9B", width: "13px", height: "13px", pointerEvents: "initial"}} className="pointer prgrss-tooltip"></div>
                  }
                })
            }
            </div>
          </div>
        </div>
       </div>
      )
    }

    let mediaLink = this.state.media ? (<h4 onClick={this.showMedia} className="container" style={{position: "absolute", cursor: "pointer", border: "1px solid white", marginLeft: "10px"}}>{this.state.media ? this.state.media.title : ""}</h4>) : "";
    let mediaTitle;
    if(this.state.showMediaTitle){
      let iconClass;
      switch(this.state.showMediaTitle.type) {
        case "pdf":
            iconClass = "fa fa-file-pdf-o";
            break;
        case "audio":
            iconClass = "fa fa-file-audio-o";
            break;
        case "video":
            iconClass = "fa fa-file-movie-o";
            break;
        case "web":
            iconClass = "fa fa-file-text-o";
            break
        default:
            iconClass = "";
      }
      mediaTitle = (
          <h4 style={{whiteSpace: "nowrap", marginLeft: `${(this.state.showMediaTitle.start/this.state.duration) * 100}%`}}><i className={iconClass} aria-hidden="true" style={{marginRight: "5px"}}></i>{this.state.showMediaTitle.title}</h4>
        )
    }
    return (
      <span>
        <div className="playerContainerv2" style={style}>
          {controls}
          <div ref={(c) => this._vidDiv = c}>
          </div>
          {mediaLink}
        </div>
        <div className="width100 flex" style={{height: "20px", padding: "10px 0"}} >
          {mediaTitle}
        </div>
      </span>
    );
  }
}

VideoEmbed.propTypes = {
  data: PropTypes.object,
  height: PropTypes.string,
  width: PropTypes.string
};


export default VideoEmbed;

