const IconVolume = props => (
      <svg onClick={props.onClick} className="volume" xmlns="http://www.w3.org/2000/svg" width="15px" height="15px" viewBox="0 0 32 32" aria-labelledby="title">
        <title id="volume"></title>
        <g> 
          <path fill={props.fill} strokeWidth={props.strokeWidth} stroke={props.stroke} d="M9.7,24.9H0V8.3h9.7V24.9z M12.5,8.3v16.6l15.2,8.3V0L12.5,8.3z"/>
        </g>
      </svg>

);

