'use strict'
import React, {PropTypes} from 'react';
import VideoEmbedv2 from './VideoEmbedv2';
import PDFEmbed from './PDFEmbed';
import AudioEmbed from './AudioEmbed';
import WebEmbed from './WebEmbed';

const mediaStatic = [{start: 5, end: 10, title: "PDF Lorem", type: "pdf", url: "https://www.research.ibm.com/software/IBMResearch/multimedia/Computing_Cognition_WhitePaper.pdf"}, {start: 15, end: 20, title: "Medium Post", newTab: true, type: "web", url: "https://medium.com/cognitivebusiness/tap-into-your-tribal-knowledge-48a3786df081"}, {start: 35, end: 40, title: "Podcast Lorem", type: "audio", url: "http://public.dhe.ibm.com/software/data/sw-library/cognos/podcasts/bi-radio/ep33-0_pd_ba-forum-ibm-cognos-10.mp3"}, {start: 55, end: 60, title: "Video Lorem", type: "video", src: "https://www.youtube.com/embed/VDjpPmnso94?list=PL4F1C783776E708A8"}, {start: 72, end: 77, title: "IBM site", type: "web", url: "http://www.ibm.com/analytics/us/en/technology/hadoop/hadoop-trials.html"}]

class Modal extends React.Component {
	constructor(props) {
    super(props);
    this.state = {
    	showMedia: null
    };
    this.showMedia = this.showMedia.bind(this);
    this.hideMedia =  this.hideMedia.bind(this);
    this.showMediaClick = this.showMediaClick.bind(this);
    this.getContent = this.getContent.bind(this);
  }

  showMedia(media){
  	if(media.newTab) {
  		window.open(media.url, '_blank');
  	} else {
			this.setState({showMedia: media})
			this._modalContainer.scrollTop = 1000;
  	}
  }  
  showMediaClick(index){
		this.setState({showMedia: mediaStatic[index]})
  }
	hideMedia(){
		this.setState({showMedia: null})
	}

  getContent(){
		if (this.state.showMedia) {
			switch (this.state.showMedia.type) {
	    case "pdf":
	        return (
					<span>
						<div className="width100 flex container" style={{flexDirection: "row-reverse", fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
							<i className="pntr" className="fa fa-times" onClick={this.hideMedia}/>
						</div>
	        	<PDFEmbed source={this.state.showMedia.url} />
	        </span>
	        )
	        break; 
	    case "audio":
	        return (
					<span>
						<div className="width100 flex container" style={{flexDirection: "row-reverse", fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
							<i className="pntr" className="fa fa-times" onClick={this.hideMedia}/>
						</div>
	        	<AudioEmbed sources={
							[
								{
									type: "mp3",
									url: this.state.showMedia.url,
								}
							]
						} />
					</span>
					)
	        break; 
	    case "web":
	        return (
					<span>
						<div className="width100 flex container" style={{flexDirection: "row-reverse", fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
							<i className="pntr" className="fa fa-times" onClick={this.hideMedia}/>
						</div>
	        	<WebEmbed source={this.state.showMedia.url} />
	        </span>)
	        break; 
	    case "video":
	    		return (
	    			<div className="width100 flex center" style={{backgroundColor: "#0D0E0E"}}>
							<div className="width100 flex container" style={{flexDirection: "row-reverse", fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
								<i className="pntr" className="fa fa-times" onClick={this.hideMedia}/>
							</div>
		    			<iframe width="560" height="315" src={this.state.showMedia.src} frameborder="0" allowfullscreen></iframe>
	        	</div>
	    		)
	    		break;
	    default: 
	        return <Default />;
			}
		} else {
			return <Default />;
		}
  }

  render() {
  	const {hideModal} = this.props;
  	const content = this.getContent();
  	return (
			<div className="modal container" ref={(c) => this._modalContainer = c}>
				<div className="width100 flex" style={{flexDirection: "row-reverse", fontSize: "2em"}}>
					<i className="fa fa-times pntr" onClick={hideModal}/>
				</div>
				<div className="flex center width100" style={{marginBottom: "30px"}}>
					<VideoEmbedv2 
						data={{
				      id: 'GFZ2IaTVkY8',
				      title: '70 Miles From Shore with Watson: Woodside Energy and IBM',
				      description: 'Lorem ipsum dasknda dsalknd salkdn',
				      parts: [{title: "The Platform", description: "Big data at its finest; the complexity of this oil rig is just numbers unless captured in a way that pays off", start: "0", end: "15"},{title: "Cognitive truth", description: "Watson consumes every bit of data and provides insights that help this engine run its smoothest", start: "20", end: "24"},{title: "Watson, the Engineer", description: "We teach Watson to think like an engineer and, in turn Watson thinks like a thousand engineers", start: "60", end: "35"}],
				      media: [{start: 5, end: 10, title: "Watson: The platform for cognitive business", type: "pdf", url: "https://www.research.ibm.com/software/IBMResearch/multimedia/Computing_Cognition_WhitePaper.pdf"}, {start: 15, end: 20, title: "How Watson works", newTab: true, type: "web", url: "https://medium.com/cognitivebusiness/tap-into-your-tribal-knowledge-48a3786df081"}, {start: 35, end: 40, title: "Innovate with the cognitive cloud", type: "audio", url: "http://public.dhe.ibm.com/software/data/sw-library/cognos/podcasts/bi-radio/ep33-0_pd_ba-forum-ibm-cognos-10.mp3"}, {start: 75, end: 80, title: "Cognitive business: The thinking business", type: "video", src: "https://www.youtube.com/embed/VDjpPmnso94?list=PL4F1C783776E708A8"}]
						}}
						height="385px"
				    width="640px"
				    showMedia={this.showMedia}
					/>
				</div>
				{
				// <div className="flex center container width100">
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 0)}>Lorem.pdf</p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}}><a style={{color: "inherit"}} target="_blank" href="https://medium.com/cognitivebusiness/tap-into-your-tribal-knowledge-48a3786df081">Medium Post</a></p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 2)}>Lorem.mp3</p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 3)}>Lorem.mp4</p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 4)}>Lorem.com</p>
				// </div>
				}
				
				<div className="container width100">
					{content}
				</div>
			</div>
    );
  }
}

Modal.propTypes = {
  
};


export default Modal;


const Default = props => (
	<span>
		<h4>Lorem ipsum dolor sit amet.</h4>
		<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Error debitis ipsam maxime consequuntur delectus nam adipisci quidem corrupti, placeat rerum, consequatur possimus obcaecati ut, magnam voluptatem, iste facilis voluptas soluta consectetur. Incidunt expedita iure animi saepe, dicta optio accusantium laborum, reprehenderit fuga sapiente accusamus nisi ad, quis libero. Rem quidem id soluta et voluptatem odit perspiciatis illum doloremque quasi voluptates voluptatibus mollitia ullam repellat molestias deserunt, magnam fugit distinctio, qui voluptate hic. Commodi, laboriosam et at tenetur veritatis dolorem illo ratione eum, iure tempore saepe delectus, harum temporibus, blanditiis numquam fugiat voluptate labore? Natus nemo magni dolorum magnam, vel animi?</p>
		<h4>Lorem ipsum dolor sit.</h4>
		<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Explicabo possimus ipsam sed qui quo ipsum, reiciendis veniam assumenda exercitationem iusto. Nam error quo perferendis ipsum, deleniti commodi, minima fugiat? Ut et veniam repellat, voluptates rerum eligendi nam minus necessitatibus excepturi dolorem cupiditate odio reiciendis quis doloremque voluptatibus tempore eaque expedita quas, amet autem praesentium maiores. Et nemo obcaecati accusantium maxime consequuntur magnam dolores incidunt mollitia aspernatur, sapiente ipsum, ab a ducimus quod voluptatibus, dolorem earum laboriosam nostrum. Dolorum alias nulla, esse illum nesciunt facilis dolorem amet culpa. Odit nisi repudiandae at, accusantium impedit molestias architecto repellat aliquid veritatis recusandae, laboriosam natus a modi reiciendis atque incidunt quis. Dignissimos, maxime et consequatur amet iusto soluta rem quo iste provident magnam voluptate non, quis repellendus alias laudantium ex modi voluptatum quasi fuga fugit dolor repudiandae, harum mollitia. Non hic, cupiditate quisquam fuga neque quis aspernatur debitis saepe sed, vitae ex quos iure!</p>
	</span>
)




